import React, { useState, useEffect } from "react";
import companyName from "./../../images/HODLINFO.8f78fc06.png";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import "./../../css/Header.css";
import { DropdownButton, Dropdown } from "react-bootstrap";

export const Header = () => {
  const [btn2text, setbtn2text] = useState("BTC");
  const [timer, setTimer] = useState(60);

  useEffect(() => {
    window.setTimeout(() => {
      if (timer == 0) setTimer(60);
      else setTimer(timer - 1);
    }, 1000);
    return () => {};
  }, [timer]);

  return (
    <div className="d-flex justify-content-between bg-dark p-4">
      <div
        className="d-flex flex-column p-2"
        style={{
          alignContent: "flex-start",
          cursor: "pointer",
          width: "40%",
        }}
      >
        <img src={companyName} className="fiat-logo" width="60%"></img>
        <p
          className="text-secondary m-0 w-50"
          style={{ textAlign: "left", verticalAlign: "middle" }}
        >
          <b>
            {" "}
            Powered By <span className="text-info">Finstreet</span>
          </b>
        </p>
      </div>
      <div
        className="d-flex align-items-center"
        style={{ position: "relative", left: "-10%" }}
      >
        <div>
          <DropdownButton
            id="dropdown-basic-button"
            className="m-2"
            variant="secondary"
            title="INR"
          >
            <Dropdown.Item href="#/action-1">INR</Dropdown.Item>
          </DropdownButton>
        </div>
        <div>
          <DropdownButton
            id="dropdown-basic-button"
            variant="secondary"
            title={btn2text}
          >
            <Dropdown.Item href="#/action-1">BTC</Dropdown.Item>
            <Dropdown.Item
              href="#/action-2"
              onClick={() => {
                setbtn2text("ETH");
              }}
            >
              ETH
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-3"
              onClick={() => {
                setbtn2text("USDT");
              }}
            >
              USDT
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-4"
              onClick={() => {
                setbtn2text("XRP");
              }}
            >
              XRP
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-5"
              onClick={() => {
                setbtn2text("TRX");
              }}
            >
              TRX
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-4"
              onClick={() => {
                setbtn2text("DASH");
              }}
            >
              DASH
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-5"
              onClick={() => {
                setbtn2text("ZEC");
              }}
            >
              ZEC
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-4"
              onClick={() => {
                setbtn2text("XEM");
              }}
            >
              XEM
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-5"
              onClick={() => {
                setbtn2text("IOST");
              }}
            >
              IOST
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-4"
              onClick={() => {
                setbtn2text("WIN");
              }}
            >
              WIN
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-5"
              onClick={() => {
                setbtn2text("BTT");
              }}
            >
              BTT
            </Dropdown.Item>
            <Dropdown.Item
              href="#/action-4"
              onClick={() => {
                setbtn2text("WRX");
              }}
            >
              WRX
            </Dropdown.Item>
          </DropdownButton>
        </div>

        <button className="btn btn-secondary m-2 ">BUY {btn2text}</button>
      </div>

      <div className="d-flex justify-content-center align-items-center">
        <div style={{ width: "8vh" }}>
          <CircularProgressbar
            value={timer}
            maxValue={60}
          ></CircularProgressbar>
        </div>
        <div
          className="text-info"
          style={{
            position: "relative",
            left: "-11%",
            fontSize: "18px",
            fontWeight: "100%",
          }}
        >
          <b>{timer}</b>
        </div>

        <div
          className="d-flex align-items-center btn btn-info pointer m-4"
          style={{ borderRadius: "10px", height: "40px" }}
        >
          <img
            src="data:image/png;base64,"
            alt="Loading..."
            height="15px"
            className="m-2"
          />
          <div className="text-nowrap text-white">
            <b>Connect Telegram</b>
          </div>
        </div>
        {/* <button className="switch">theme</button> */}
        <label className="switch">
          <input type="checkbox" checked />
          <span className="slider round"></span>
        </label>
      </div>
    </div>
  );
};
